#content {
    display: flex;
    place-content: center;
    place-items: center;
    flex-direction: column;
    box-sizing: border-box;
    width: 100%;
    max-width: 100vw;
    overflow-x: hidden;
    min-height: 60vh;
}

.content_container {
    margin-top: 50px;
    width: 100%;
}
.s_c_padding {
    padding: 0 150px;
}

@media screen and (max-width: 1200px) {
    .s_c_padding {
        padding: 0 50px;
    }
}

@media screen and (max-width: 992px) {
    .s_c_padding {
        padding: 0 20px;
    }
}
